
    <template>
  <div>
    <v-main>
      <v-layout style="background:#fff" class="py-5 px-3" align-center justify-center wrap column>
        <v-flex xs12 md12>
          <v-layout wrap>
            <v-flex xs12 md1></v-flex>
            <v-flex xs12 md7 pr-5>
              <p class="heading">Join us</p>
              <p
                class="text"
              >Those who help humanity reap the fruits of progress by sowing the seeds with their own two hands, must do so knowing that they may never taste the fruits themselves.</p>
              <p class="heading">Membership Rules</p>
               <p class="text">
                 1. Membership is available to anyone 18 years of age or older.
<br/>
2. You promise NOT to use or to conduct any fraudulent or business activity or have more than one Member Account at any time.
               </p>
              <p class="heading">Privacy Policy</p>
  <p class="text">
    Your information is collected to verify your existence and we will not be shared anywhere else without your consent.
  </p>
            </v-flex>
            <v-flex xs12 md4 hidden-sm-and-down>
              <v-img :src="contactuslogo" height="auto" width="50%"></v-img>
            </v-flex>
          </v-layout>
        </v-flex>
      </v-layout>
    </v-main>
    <iframe name="iframe" style="display:none" ></iframe>
    <v-layout row wrap align-center justify-center mt-3>
      <v-flex xs10 md8 text-center>
        <v-card class="mx-auto elevation-15" style="border-radius:8px" v-if="status">
          <v-card-text>
            <v-form
              ref="form"
              v-model="valid"
              action="https://docs.google.com/forms/u/0/d/e/1FAIpQLScF6jOCYsX_LDva0wSChqjE3rcOkfc5aJyqkrz0-bplp3dPbA/formResponse"
              method="POST"
              target="iframe"
            >
              <v-text-field
                :value="name"
                type="text"
                label="Name"
                name="entry.2076710276"
                counter
                :rules="nameRules"
                color="blue"
              ></v-text-field>
              <v-text-field
                :value="value"
                label="Phone (prefer whatsapp)"
                name="entry.1148496798"
                color="green"
                counter
                :rules="phoneRules"
              ></v-text-field>
              <v-text-field
                :value="value"
                label="Email"
                type="email"
                name="entry.2648476"
                color="yellow"
                counter
                :rules="nameRules"
              ></v-text-field>
              <v-textarea
                :value="value"
                label="Address"
                type="text"
                name="entry.65524481"
                
                color="red"
                counter
                :rules="nameRules"
              ></v-textarea>
              <v-text-field
                :value="value"
                label="Date of Birth"
                type="text"
                name="entry.91054441"
                color="blue"
                counter
                :rules="nameRules"
              ></v-text-field>
              <v-select
              name="entry.10658582"
          :items="items"
          label="Where did you hear about us?"
          v-model="valueother"
          color="green"
        ></v-select>
        <v-text-field
                :value="value"
                label="Please type other option here"
                type="text"
                name="entry.1594046172"
                color="red"
                counter
                
                :disabled="!other"
              ></v-text-field>
              <v-textarea
                :value="value"
                label="Why are you interested in joining Rotaract?"
                type="text"
                name="entry.898993855"
                color="blue"
                counter
                :rules="nameRules"
              ></v-textarea>
              
   

              <v-btn
                @click="callme"
                :disabled="!valid"
                type="submit"
                color="#F60044"
                v-show="show"
              >{{text}}</v-btn>

              <v-btn
                @click="callme2"
                color="red lighten-1"
                class="white--text"
                v-show="!show"
              >Clear All</v-btn>
            </v-form>
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>


    <v-snackbar v-model="snackbar" color="green" :timeout="timeout">
      Thanks For your Response.
      We will respond you soon...!
      <v-btn color="white" @click="callme1" class="pink--text" small>Clear</v-btn>
    </v-snackbar>
    <v-snackbar
      v-if="!status"
      v-model="checknetwork"
      color="red"
      :timeout="timeoutstatus"
    >You are Offline, Check Your Network Connection to Fill Form.</v-snackbar>
  </div>
</template>

<script>
import contactus from "@/assets/logos/joinus.svg";

export default {
  components: {
  
  },
  data() {
    return {
      value1: "",
      value: "",
      valueother:"",
      snackbar: false,
      other: this.valueother == "Others" ? true : false,
      valid: true,
      text: "submit",
      show: true,
      timeout: 8000,
      timeoutstatus: 0,
      contactuslogo: contactus,
      checknetwork: true,
      name: "",
      nameRules: [v => !!v || "Required field"],
      status: true,
      phoneRules: [
          (v) => !!v || 'Name is required',
          (v) => v && v.length <= 10 || 'Number must be less than 10 characters'
        ],
        items: ["Friend", "Google", "Social media", "News Article", "Others"]
    };
  },
   mounted() {
    document.title = "Rotract | Join";
    this.status = navigator.onLine;
     window.addEventListener("offline", () => {
            this.status = false;
        });
        window.addEventListener("online", () => {
            this.status = true
        });
  },
  updated(){
    if (this.valueother == "Others"){
      this.other = true
    }else {
      this.other = false
    }
  },
  methods: {
    callme() {
      this.text = "submit";
      this.snackbar = true;
      this.show = false;
    },
    callme1() {
      this.text = "submit";
      this.snackbar = false;
      this.$refs.form.reset();
      this.show = true;
    },
    callme2() {
      this.show = true;
      this.$refs.form.reset();
    }
  }
};
</script>

